import { createWebHistory, createRouter } from "vue-router";//vue-router 라이브러리 이름
import  vueList from './components/vueList.vue';
import  homeVue from './components/homeVue.vue';
import  detailVue from './components/detailVue.vue';
import  Author from './components/Author.vue';
import  Comment from './components/Comment.vue';

const routes = [
  {
    path: "/detail/:id",
    component: detailVue,
    children:[
      {
        path:"author",
        component:Author,
      },
      {
        path:"comment",
        component:Comment,
      }
    ]
  },
  /*
  {
    path: "/detail/:id(\\d+)",//정규식 문법 \\d+
    component: detailVue,
  },
  */
  {
    path: "/",
    component: homeVue,
    /* 여러개 컴포넌트 동시에 보여줘야 할때 : router-view 도 3개 넣어두어야함 아래의 경우
    components: {
        default: Home,
        // short for LeftSidebar: LeftSidebar
        LeftSidebar,
        // they match the `name` attribute on `<router-view>`
        RightSidebar,
      },
    
    
    */
  },
  {
    path: "/list",
    component: vueList,
  },
  
 /*  { 404페이지
    path: "/:anything(.*)",
    component: homeVue,
  }, */
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 